.root{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 80px;
  background: #49cae9;
  padding: 20px;
  box-sizing: border-box;
  border-bottom:1px solid #eeeeee;
  color:#ffffff;

  :global{

    .bar{
      width: 100%;
      display:flex;
      align-items:center;
      justify-content:space-between;
      .back-btn{
        width: 64px;
        box-sizing: border-box;
        padding: 10px;
        font-size: 16px;
        letter-spacing: 2px;
        background: #000000;
        color: #ffffff;
        border-radius: 20px;
      }
      .orange{
        background: orange;
      }
      .title{
        font-size: 18px;
        letter-spacing: 2px;
        padding: 0 5px;
        text-align: center;
      }
      .box{
        width: 64px;
      }
    }
    .video{
      margin-top: 20px;
      border-radius: 20px;
      width: 100%;
      background: #ffffff;
    }
  }
}
