.root{
  width: 100%;
  padding: 0 20px;
  box-sizing:border-box;
  height:100vh;
  overflow-y:auto;

  :global{
    .header{
      position: fixed;
      width: 100%;
      top: 0;
      left: 0;
      height: 50px;
      line-height: 50px;
      text-align: center;
      font-size: 18px;
      border-bottom: 1px solid #eeeeee;
      background: #ffffff;
      z-index: 999;
    }
    .collapse{
      margin-top: 50px;
      box-sizing: border-box;
    }
    .space{
      width: 100%;
    }
    .card{
      width: 100%;
      box-sizing: border-box;
      color: #000000;
      border: 1px solid #000000;

      &--active{
        background: #000000;
        color: #ffffff;
      }
    }
  }
}
